body {
  background-color: black;
  color: white;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
}
.header {
  background: black;
}
.header .wrapper {
  display: flex;
  justify-content: space-around;
}
.header__menu {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.header__menu-item {
  width: 20%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: auto;
  background-color: white;
}
.header__menu-item a {
  color: black;
}
